<template>
  <div>
    <section
      id="welcome"
      class="overflow-hidden"
    >
      <v-row no-gutters>
        <v-col
          class="hidden-sm-and-down"
          md="6"
        >
          <v-img
            :src="require('@/assets/img/image1.jpg')"
            height="100vh"
          />
        </v-col>

        <v-col
          class="align-content-space-between layout wrap"
          cols="12"
          md="6"
        >
          <base-bubble-1 style="transform: rotate(180deg) translateY(25%)" />

          <v-row
            align="center"
            justify="center"
          >
            <v-col
              cols="12"
              md="6"
            >
              <base-heading class="mt-16 text-h4">
                Selamat Datang
              </base-heading>
              <base-text>
                Daeng Kurir Makassar, melayani jemput paket gratis, diantarkan
                bayar di tujuan, belanja online COD, bayar CASH di rumah, ongkir
                mulai 10rb ke seluruh wilayah kota makassar.
              </base-text>
              <v-btn
                class="ma-2 pa-3 pink--text tour-1"
                :x-large="isLarge"
                :large="isXSmall"
                @click="$store.commit('dialog', true)"
              >
                Order Sekarang
                <v-img
                  src="@/assets/img/dkm.png"
                  :width="isXSmall ? 25 : 40"
                  class="my-6"
                />
              </v-btn>
            </v-col>
          </v-row>

          <base-bubble-2
            style="transform: rotate(180deg) translate(-200px, -15%)"
          />
        </v-col>
      </v-row>
      <login-vue
        v-if="this.$store.state.Authorization == false"
        v-show="this.$store.state.dialog"
      />
      <order-vue
        v-else
        v-show="this.$store.state.dialog"
      />

      <v-tour
        v-if="
          this.$store.state.authentication !== null &&
            this.$store.state.tour === true
        "
        name="myTour"
        :steps="steps"
      >
        <template slot-scope="tour">
          <transition name="fade">
            <v-step
              v-if="tour.steps[tour.currentStep]"
              :key="tour.currentStep"
              :step="tour.steps[tour.currentStep]"
              :previous-step="tour.previousStep"
              :next-step="tour.nextStep"
              :stop="tour.stop"
              :skip="tour.skip"
              :is-first="tour.isFirst"
              :is-last="tour.isLast"
              :labels="tour.labels"
            >
              <template>
                <div slot="actions">
                  <v-btn
                    x-small
                    outlined
                    class="mx-1 white"
                    @click="tour.nextStep"
                  >
                    Selanjutnya
                  </v-btn>
                  <v-btn
                    x-small
                    outlined
                    class="mx-1 white"
                    @click="tour.stop, $store.commit('tour', false)"
                  >
                    Selesai
                  </v-btn>
                </div>
              </template>
            </v-step>
          </transition>
        </template>
      </v-tour>
    </section>
  </div>
</template>
<script>
  import axios from 'axios'
  import LoginVue from './login.vue'
  import OrderVue from './Order.vue'

  export default {
    components: {
      LoginVue,
      OrderVue,
    },

    data () {
      return {
        steps: [
          {
            target: '.tour-1',
            header: {
              title: 'Selamat Datang',
            },
            content: 'Gunakan Tombol ini untuk order kurir',
          },
          {
            target: '.tour-2',
            header: {
              title: 'Selamat Datang',
            },
            content: 'Gunakan Menu ini untuk lihat daftar order anda',
          },
        ],
        isLarge: this.$vuetify.breakpoint.lgAndUp,
        isSmall: this.$vuetify.breakpoint.mdOnly,
        isXSmall: this.$vuetify.breakpoint.smAndDown,
      }
    },

    mounted () {
      this.check_auth()
      this.$tours.myTour.start()
    },

    methods: {
      check_auth () {
        axios.get('customer').then((res) => {
          this.$store.commit('district', res.data.districts)
          this.$store.commit('village', res.data.village)
          this.$store.commit('city', res.data.special_city)
          this.$store.commit('methods', res.data.methods)
          this.$store.commit('del_fee_list', res.data.del_fee_list)
        })
      },
    },
  }
</script>
