<template>
  <div>
    <v-stepper v-model="e1">
      <v-stepper-header>
        <v-stepper-step
          :complete="e1 > 1"
          step="1"
        >
          Data Pengirim
        </v-stepper-step>

        <v-divider />

        <v-stepper-step
          :complete="e1 > 2"
          step="2"
        >
          Data Penerima
        </v-stepper-step>

        <v-divider />

        <v-stepper-step
          :complete="e1 > 3"
          step="3"
        >
          Data Barang
        </v-stepper-step>
        <v-divider />
        <v-stepper-step step="4">
          Selesai
        </v-stepper-step>
      </v-stepper-header>

      <v-stepper-items>
        <!-- Pengirim -->
        <v-stepper-content step="1">
          <v-form>
            <v-row>
              <v-col
                cols="12"
                md="6"
                sm="12"
                class="px-9"
              >
                <v-text-field
                  v-model="name"
                  :error-messages="nameErrors"
                  label="Nama"
                  required
                  @input="$v.name.$touch()"
                  @blur="$v.name.$touch()"
                />
                <v-text-field
                  v-model="email"
                  :error-messages="emailErrors"
                  label="E-mail"
                  required
                  @input="$v.email.$touch()"
                  @blur="$v.email.$touch()"
                />
                <v-text-field
                  v-model="phone"
                  :error-messages="phoneErrors"
                  label="phone"
                  required
                  @input="$v.phone.$touch()"
                  @blur="$v.phone.$touch()"
                />
                <v-text-field
                  v-model="phone2"
                  label="phone2 (Terhubung dengan whatsapp)"
                />
                <v-text-field
                  v-if="
                    formType === 'create' &&
                      this.$store.state.authentication == null
                  "
                  v-model="password"
                  :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                  :type="show1 ? 'text' : 'password'"
                  :error-messages="passwordErrors"
                  label="Password"
                  required
                  @input="$v.password.$touch()"
                  @blur="$v.password.$touch()"
                  @click:append="show1 = !show1"
                />

                <v-text-field
                  v-if="
                    formType === 'create' &&
                      this.$store.state.authentication == null
                  "
                  v-model="passwordConfirmation"
                  :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                  :type="show1 ? 'text' : 'password'"
                  :error-messages="passwordConfirmationErrors"
                  label="Password Confirmation"
                  required
                  @input="$v.passwordConfirmation.$touch()"
                  @blur="$v.passwordConfirmation.$touch()"
                  @click:append="show1 = !show1"
                />
                <v-select
                  v-model="city_id"
                  :items="this.$store.state.city"
                  item-text="nama"
                  item-value="city_id"
                  label="Kota"
                  required
                  @change="filterDistrictUser()"
                />
                <v-select
                  v-model="district_id"
                  :items="this.$store.state.district"
                  item-text="nama"
                  item-value="id"
                  label="Kecamatan"
                  :disabled="city_id === null"
                  required
                  @change="filterVillageUser"
                />
                <v-select
                  v-if="district_id !== null"
                  v-model="village_id"
                  :items="filteredVillage"
                  item-text="nama"
                  item-value="id"
                  label="Kelurahan/Desa"
                  required
                />

                <v-select
                  v-else
                  v-model="village_id"
                  disabled
                  :items="filteredVillage"
                  item-text="nama"
                  item-value="id"
                  label="Kelurahan/Desa"
                />
              </v-col>
              <v-col class="px-9 pt-8">
                <!-- <v-select
                  v-model="role_id"
                  :items="role"
                  item-text="name"
                  item-value="id"
                  :error-messages="roleErrors"
                  label="Role"
                  required
                  @change="$v.role.$touch()"
                  @blur="$v.role.$touch()"
                /> -->
                <v-textarea
                  v-model="address"
                  label="Alamat"
                  value=""
                  hint="Masukan Alamat"
                />
                <v-file-input
                  v-if="this.$store.state.authentication == null"
                  v-model="photo"
                  :rules="rules"
                  accept="image/jpeg"
                  prepend-icon="mdi-camera"
                  label="Gambar"
                  @change="preview($event)"
                />
                <div v-if="imgUrl">
                  <v-img
                    class="preview"
                    :src="imgUrl"
                  />
                </div>
              </v-col>
            </v-row>
          </v-form>
          <v-btn
            v-if="this.$store.state.authentication === null"
            dark
            :small="isXSmall"
            color="green"
            class="float-right"
            :disabled="name == null"
            @click="saveUser(), sendEmail()"
          >
            Simpan dan Lanjutkan
            <v-icon>mdi-arrow-right</v-icon>
          </v-btn>
          <v-btn
            v-else
            dark
            :small="isXSmall"
            color="green"
            class="float-right"
            :disabled="name == null"
            @click="updateUser"
          >
            Simpan dan Lanjutkan
            <v-icon :small="isXSmall">
              mdi-arrow-right
            </v-icon>
          </v-btn>
        </v-stepper-content>
        <!-- Barang -->
        <v-stepper-content step="3">
          <v-form>
            <v-container class="py-0">
              <v-row>
                <v-col
                  cols="12"
                  md="4"
                >
                  <v-text-field
                    v-model="form.productName"
                    label="Nama Produk"
                    outlined
                    clearable
                  />
                  <v-text-field
                    v-model="form.weight"
                    label="Berat"
                    type="number"
                    outlined
                    clearable
                    required
                    @input="countFee"
                  />
                  <v-text-field
                    v-model="form.volume"
                    label="Volume"
                    type="number"
                    outlined
                    clearable
                  />
                  <vuetify-money
                    v-model="form.delivery_fee"
                    label="Ongkos Kirim"
                    readonly
                    :outlined="outlined"
                    :options="options"
                  />
                  <vuetify-money
                    v-model="form.price"
                    :label="label"
                    :readonly="readonly"
                    :disabled="disabled"
                    :outlined="outlined"
                    :clearable="clearable"
                    :options="options"
                  />
                  <v-textarea
                    v-model="form.description"
                    label="Deskripsi"
                    type="number"
                    outlined
                    clearable
                  />
                </v-col>
                <v-col
                  cols="12"
                  md="4"
                >
                  <v-file-input
                    v-model="form.photo"
                    accept="image/jpeg"
                    prepend-icon=""
                    label="Photo"
                    clearable
                    outlined
                    @change="preview($event)"
                  />
                  <v-card
                    class="d-flex justify-space-around pa-4 mb-6"
                    style="border:dashed; border-color: gray;"
                  >
                    <v-img
                      v-if="imgUrl"
                      class="preview"
                      :src="imgUrl"
                    />
                    <v-card-text
                      v-else
                      class="text-center"
                      style="height:200px"
                    >
                      Image Preview Area
                    </v-card-text>
                  </v-card>
                  <v-select
                    v-model="form.selectedMethod"
                    :items="this.$store.state.methods"
                    :error-messages="paymentMethodErrors"
                    item-text="method"
                    item-value="id"
                    label="Metode Pembayaran"
                    outlined
                    clearable
                    @change="$v.form.selectedMethod.$touch()"
                  />
                  <div class="text-h6">
                    Kategori Order
                  </div>
                  <v-radio-group
                    v-model="category"
                    :error-messages="categoryErrors"
                    row
                    @input="$v.category.$touch()"
                    @blur="$v.category.$touch()"
                  >
                    <v-radio
                      label="REGULER"
                      style="font-weight: bold"
                      value="1"
                    />
                    <v-radio
                      label="EXPRESS"
                      style="font-weight: bold"
                      value="2"
                    />
                  </v-radio-group>
                </v-col>
              </v-row>
            </v-container>
          </v-form>
          <v-btn
            :small="isXSmall"
            class="float-right ml-5 my-2"
            color="green"
            dark
            @click="saveOrder(2)"
          >
            CHECKOUT & ORDER LAGI
          </v-btn>
          <v-btn
            :small="isXSmall"
            class="float-right ml-5 my-2"
            color="green"
            dark
            @click="saveOrder(1)"
          >
            CHECKOUT
            <v-icon
              :small="isXSmall"
              class="ml-2"
            >
              mdi-truck-check-outline
            </v-icon>
          </v-btn>
          <v-btn
            :small="isXSmall"
            color="green"
            dark
            class="float-right ml-5 my-2"
            @click="e1 = 2"
          >
            <v-icon :small="isXSmall">
              mdi-arrow-left
            </v-icon>Sebelumnya
          </v-btn>
        </v-stepper-content>
        <!-- data penerima -->
        <v-stepper-content step="2">
          <v-row>
            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="form.receiverName"
                label="Nama"
                outlined
                clearable
              />
              <v-text-field
                v-model="form.receiverPhone"
                label="No HP"
                outlined
                clearable
              />
              <v-text-field
                v-model="form.receiverPhone2"
                label="No HP 2"
                outlined
                clearable
              />
              <v-select
                v-model="form.selectedCity"
                :items="this.$store.state.city"
                item-text="nama"
                item-value="city_id"
                label="Kota"
                outlined
                persistent-hint
                clearable
                @change="filterDistrict()"
              />
              <v-select
                v-model="form.selectedDistrict"
                :items="filteredDistrict"
                item-text="nama"
                item-value="id"
                label="Kecamatan"
                :disabled="form.selectedCity === null"
                outlined
                persistent-hint
                clearable
                @change="filterVillage()"
              />
              <v-select
                v-if="form.selectedDistrict !== null"
                v-model="form.selectedVillage"
                :items="filteredVillageO"
                item-text="nama"
                item-value="id"
                label="Kelurahan/Desa"
                outlined
                persistent-hint
                clearable
                @change="deliveryFee()"
              />
              <!-- disabled before pick disctrict -->
              <v-select
                v-else
                v-model="form.selectedVillage"
                :items="this.$store.state.village"
                item-text="nama"
                item-value="id"
                label="Kelurahan/Desa"
                disabled
                outlined
                persistent-hint
              />
            </v-col>
            <v-col
              cols="12"
              md="4"
            >
              <v-textarea
                v-model="form.receiverAddress"
                label="Alamat"
                outlined
                clearable
              />
            </v-col>
          </v-row>
          <v-btn
            :small="isXSmall"
            color="green"
            dark
            class="float-right ml-5 mt-5"
            @click="e1 = 3"
          >
            Selanjutnya<v-icon small>
              mdi-arrow-right
            </v-icon>
          </v-btn>
          <v-btn
            :small="isXSmall"
            color="green"
            dark
            class="float-right mt-5"
            @click="e1 = 1"
          >
            <v-icon small>
              mdi-arrow-left
            </v-icon> Sebelumnya
          </v-btn>
        </v-stepper-content>
        <v-stepper-content
          step="4"
        >
          <div class="text-h6 text-center">
            Terima Kasih <br>
          </div>
          <v-img
            class="mx-auto hidden-sm-and-down "
            src="@/assets/img/drive.gif"
            width="30%"
          />
          <v-img
            class="mx-auto hidden-md-and-up"
            src="@/assets/img/drive.gif"
            width="60%"
          />
          <v-btn
            :x-small="isXSmall"
            color="green"
            dark
            class="float-right mx-2"
            @click="e1 = 1"
          >
            ORDER LAGI
          </v-btn>
          <v-btn
            :x-small="isXSmall"
            color="green"
            dark
            class="float-right"
            to="/customer-page"
          >
            LIHAT DAFTAR ORDER
          </v-btn>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
  </div>
</template>
<script>
  import axios from 'axios'
  import { validationMixin } from 'vuelidate'
  import {
    required,
    maxLength,
    email,
    minLength,
    sameAs,
  } from 'vuelidate/lib/validators'
  export default {
    mixins: [validationMixin],

    validations: {
      name: { required, maxLength: maxLength(100) },
      email: { required, email },
      password: { required, minLength: minLength(8) },
      role: { required },
      passwordConfirmation: {
        required,
        sameAsPassword: sameAs('password'),
        minLength: minLength(8),
      },
      form: {
        weight: { required },
        selectedMethod: { required },
      },
      phone: { required, minLength: minLength(10) },
      category: { required },

    },
    data () {
      return {
        isLarge: this.$vuetify.breakpoint.lgAndUp,
        isSmall: this.$vuetify.breakpoint.mdOnly,
        isXSmall: this.$vuetify.breakpoint.smAndDown,
        rules: [
          value =>
            !value ||
            value.size < 2000000 ||
            'Avatar size should be less than 2 MB!',
        ],
        imgUrl: null,
        filteredVillage: [],
        filteredVillageO: [],
        filteredDistrict: [],
        additionalFee: 0,
        additionalFee2: 0,
        password: '',
        submitStatus: null,
        passwordConfirmation: '',
        city_id: null,
        district_id: null,
        village_id: null,
        row: null,
        form: {
          selectedUser: null,
          selectedCity: null,
          selectedDistrict: null,
          selectedVillage: null,
          selectedMethod: null,
          productName: null,
          weight: 0,
          volume: 0,
          price: 0,
          description: null,
          photo: null,
          receiverName: null,
          receiverAddress: null,
          receiverPhone: null,
          receiverPhone2: null,
          delivery_fee: 0,
        },
        e1: 1,
        category: null,
        show1: false,
        name: null,
        email: '',
        phone: '',
        phone2: '',
        address: '',
        role_id: 5,
        city: [],
        district: [],
        village: [],
        photo: null,
        role: [],
        formType: 'create',
        value: 0,
        label: 'Harga',
        readonly: false,
        disabled: false,
        outlined: true,
        clearable: true,
        options: {
          locale: 'pt-BR',
          prefix: 'Rp ',
          suffix: ' ',
          length: 12,
          precision: 0,
        },
        // feelist: this.del_fee_list,
      }
    },
    computed: {
      nameErrors () {
        const errors = []
        if (!this.$v.name.$dirty) return errors
        !this.$v.name.maxLength &&
          errors.push('Name must be at most 30 characters long')
        !this.$v.name.required && errors.push('Name is required.')
        return errors
      },
      emailErrors () {
        const errors = []
        if (!this.$v.email.$dirty) return errors
        !this.$v.email.email && errors.push('Must be valid e-mail')
        !this.$v.email.required && errors.push('E-mail is required')
        return errors
      },
      passwordErrors () {
        const errors = []
        if (!this.$v.password.$dirty) return errors
        !this.$v.password.minLength && errors.push('At Least 8 Characters')
        !this.$v.password.required && errors.push('Password is required')
        return errors
      },

      passwordConfirmationErrors () {
        const errors = []
        if (!this.$v.passwordConfirmation.$dirty) return errors
        !this.$v.passwordConfirmation.sameAsPassword &&
          errors.push('Passwords must be identical')
        !this.$v.passwordConfirmation.required &&
          errors.push('Password Confirmation is required')
        return errors
      },
      phoneErrors () {
        const errors = []
        if (!this.$v.phone.$dirty) return errors
        !this.$v.phone.minLength &&
          errors.push('Phone must be at most 12 characters long')
        !this.$v.phone.required && errors.push('phone is required.')
        return errors
      },
      paymentMethodErrors () {
        const errors = []
        if (!this.$v.form.selectedMethod.$dirty) return errors
        !this.$v.form.selectedMethod.required && errors.push('Pilih Metode Pembayaran')
        return errors
      },

      // weightErrors () {
      //   const errors = []
      //   if (!this.$v.form.weight.$dirty) return errors
      //   !this.$v.form.weight.required && errors.push('Berat tidak boleh kosong')
      //   return errors
      // },

      categoryErrors () {
        const errors = []
        if (!this.$v.category.$dirty) return errors
        !this.$v.category.required && errors.push('Kategori order tidak boleh kosong')
        return errors
      },
    },

    mounted () {
      this.cond()
    },

    methods: {
      cond () {
        if (this.$store.state.authentication !== null) {
          const u = this.$store.state.users
          this.name = u.name
          this.password = u.password
          this.email = u.email
          this.phone = u.phone
          this.phone2 = u.phone2
          this.address = u.address
          this.city_id = u.city
          this.district_id = u.district
          this.village_id = u.village
          this.filterVillageUser()
        }
      },
      filterDistrict () {
        // console.log(this.$store.state.village)
        this.filteredDistrict = []
        this.filteredDistrict = this.$store.state.district.filter(
          district => district.kabupaten_id === this.form.selectedCity,
        )
      },

      filterDistrictUser () {
        // console.log(this.$store.state.village)
        this.filteredDistrict = []
        this.filteredDistrict = this.$store.state.district.filter(
          district => district.kabupaten_id === this.city_id,
        )
      },

      filterVillage () {
        // console.log(this.$store.state.village)
        this.filteredVillageO = []
        this.filteredVillageO = this.$store.state.village.filter(
          village => village.kecamatan_id === this.form.selectedDistrict,
        )
      },

      filterVillageUser () {
        // console.log(this.$store.state.village)
        this.filteredVillage = []
        this.filteredVillage = this.$store.state.village.filter(
          village => village.kecamatan_id === this.district_id,
        )
      },

      preview (e) {
        if (this.form.photo !== null) {
          this.form.photo = e
          this.imgUrl = URL.createObjectURL(this.form.photo)
        } else {
          this.imgUrl = null
        }
      },

      countFee () {
        axios.get('customer/pickup-fee').then((res) => {
          this.additionalFee = res.data
          this.form.delivery_fee = 0
          const w = this.form.weight
          const list = this.$store.state.del_fee_list
          list.forEach(item => {
            if (w > parseInt(item.from_weight) && w <= parseInt(item.to_weight)) {
              this.form.delivery_fee = parseInt(item.price) + parseInt(this.additionalFee) + parseInt(this.additionalFee2)
            }
          })
        })
      },

      saveOrder (param) {
        // this.$v.$touch()
        // if (this.$v.$invalid) {
        //   console.log(this.$v.$invalid)
        //   this.submitStatus = 'ERROR'
        // } else {
        this.$swal.showLoading()
        const form = new FormData()
        form.append('name', this.form.productName)
        form.append('weight', this.form.weight)
        form.append('volume', this.form.volume)
        form.append('price', this.form.price)
        form.append('description_order', this.form.description)
        form.append('photo', this.form.photo)
        form.append('receiver_name', this.form.receiverName)
        form.append('receiver_phone', this.form.receiverPhone)
        form.append('receiver_phone2', this.form.receiverPhone2)
        form.append('receiver_address', this.form.receiverAddress)
        form.append('district', this.form.selectedDistrict)
        form.append('village', this.form.selectedVillage)
        form.append('payment_method', this.form.selectedMethod)
        form.append('user_id', this.$store.state.users.id)
        form.append('delivery_fee', this.form.delivery_fee)
        if (this.category === '1') {
          axios
            .post('customer/order', form)
            .then(() => {
              this.$swal.close()
              this.form.productName = null
              this.form.weight = 0
              this.form.volume = 0
              this.form.price = 0
              this.form.description = null
              this.form.photo = null
              this.form.receiverName = null
              this.form.receiverAddress = null
              this.form.selectedDistrict = null
              this.form.selectedVillage = null
              this.form.selectedMethod = null
              this.form.selectedUser = null
              if (param === 2) {
                this.e1 = 2
              } else {
                this.e1 = 4
              }
            })
            .catch(error => {
              console.log(error)
            })
        } else {
          axios
            .post('customer/order-exp', form)
            .then(() => {
              this.$swal.close()
              this.form.productName = null
              this.form.weight = 0
              this.form.volume = 0
              this.form.price = 0
              this.form.description = null
              this.form.photo = null
              this.form.receiverName = null
              this.form.receiverAddress = null
              this.form.selectedDistrict = null
              this.form.selectedVillage = null
              this.form.selectedMethod = null
              this.form.selectedUser = null
              if (param === 2) {
                this.e1 = 2
              } else {
                this.e1 = 4
              }
            })
            .catch(error => {
              console.log(error)
            })
        }
        // }
      },
      saveUser () {
        // this.$v.$touch()
        // if (this.$v.$invalid) {
        //   console.log(this.$v.$invalid)
        //   this.submitStatus = 'ERROR'
        // } else {
        this.$swal.showLoading()
        const form = new FormData()
        form.append('name', this.name)
        form.append('email', this.email)
        form.append('password', this.password)
        form.append('password_confirmation', this.passwordConfirmation)
        form.append('role_id', this.role_id)
        form.append('address', this.address)
        form.append('phone', this.phone)
        form.append('phone2', this.phone2)
        form.append('photo', this.photo)
        form.append('city_id', this.city_id)
        form.append('district_id', this.district_id)
        form.append('village_id', this.village_id)

        axios.post('auth/register', form).then(() => {
          this.signIn()
          this.$v.$reset()
          this.name = ''
          this.passwordConfirmation = ''
          this.select = null
          this.photo = null
          this.phone = null
          this.address = null
          this.role_id = null
          this.e1 = 2
          this.$swal.close()
        })

          .catch((error) => {
            const msg = error.response.data.email[0]
            console.log(msg)
            this.$swal(msg, '', 'error')
          })
        // }
      },

      sendEmail () {
        axios.get('/send/' + this.email).then(() => {
        })
      },

      updateUser () {
        this.$swal.showLoading()
        const form = new FormData()
        form.append('id', this.$store.state.users.id)
        form.append('name', this.name)
        form.append('email', this.email)
        form.append('address', this.address)
        form.append('role_id', this.role_id)
        form.append('phone', this.phone)
        form.append('phone2', this.phone2)
        form.append('photo', this.photo)
        form.append('city_id', this.city_id)
        form.append('district_id', this.district_id)
        form.append('village_id', this.village_id)
        axios.post('auth/update', form).then(() => {
          this.e1 = 2
          this.tab = 'menu-1'
          this.$swal.close()
          this.getUsers()
        })
      },

      getUsers () {
        axios
          .get('customer/user')
          .then(res => {
            this.$store.commit('users', res.data.data)
          })
          .catch(error => {
            console.log(error)
          })
      },

      signIn () {
        this.loading = true
        axios
          .post('/auth/login', {
            email: this.email,
            password: this.password,
          })
          .then(res => {
            // console.log(res.data);
            axios.defaults.headers.common.Authorization =
              'Bearer ' + res.data.token
            this.$store.commit('authentication', res.data.token)
            this.$store.commit('Authorization', true)
            this.$store.commit('users', res.data.users)
            this.loading = false
          // this.$store.commit('dialog', false)
          })
          .catch(error => {
            alert(error)
            console.log(error)
          })
      },

      openForm () {
        this.$store.commit('Authorization', true)
      },

      deliveryFee () {
        axios.get('customer/special-delivery-fee/' + this.form.selectedVillage).then((res) => {
          this.additionalFee2 = res.data
        })
      },
    },
  }
</script>
